<template>
  <div class="search pageSpace h100">
    <my-breadcrumb></my-breadcrumb>
    <div class="pageContent">
      <a-layout class="w100 h100">
        <a-layout-sider width="300" style="overflow-y: auto">
          <div v-if="treeData.length > 0">
            <a-tree
              :tree-data="treeData"
              :show-line="true"
              :defaultExpandedKeys="defaultExpandedKeys"
              :defaultSelectedKeys="defaultSelctedKeys"
              :replaceFields="replaceFields"
              @select="nodeClick"
            ></a-tree>
          </div>
          <div v-else>
            <a-alert type="info" message="未发现数据"></a-alert>
          </div>
        </a-layout-sider>
        <a-layout class="in-content">
            <log-search :curNode = "currentNode" ref="searchLogStart"></log-search>
        </a-layout>
      </a-layout>
    </div>
    <!-- <a-layout class="bt h100">
            
            <slider-menu :defaultModule="selectRoute" :menus="objByUnit"></slider-menu>
            <a-layout-content>
                <router-view />
            </a-layout-content>
        </a-layout> -->
  </div>
</template>
<script>
import { mapState } from "vuex";
import logSearch from './logSearch.vue';
export default {
  components: { logSearch },
  name: "search",
  data() {
    return {
      treeData: [],
      replaceFields: {
        children: "children",
        title: "name",
        key: "id",
      },
      dicData: [],
      currentNode: {},
      defaultSelctedKeys: [],
      defaultExpandedKeys:[]
    };
  },
  watch: {
    unitId: {
      handler: function (unitId) {
        this.getTreeData(unitId);
      },
    },
  },
  methods: {
      // 获取树结构数据
      getTreeData(unitId){
          this.$API.searchDicBelong(unitId).then(res => {
              if(res.code === 1000){
                  this.treeData = res.data;
                  this.defaultSelctedKeys = [this.treeData[0].id];
                  this.defaultExpandedKeys = this.treeData.map(list => list.id);
                  this.currentNode = this.treeData[0];
              } else {
                  this.treeData = [];
                  this.$message.warning(res.message);
              }
          })
      },
      // 点击节点触发
      nodeClick(key,e){
        let selectNode = e.selectedNodes[0].data.props;
        this.currentNode = selectNode;
        // this.$refs.searchLogStart.search();
      },
  },
  created() {
    if(this.unitId){
      this.getTreeData(this.unitId);
    }
  },
  computed: {
    ...mapState(["unitId"]),
  },
};
</script>
<style>
.search .ant-layout-sider {
  border-right: 1px solid #5dc2fe;
  padding-right: 10px;
}
.search .ant-layout-sider,
.search .ant-layout {
  background: transparent;
}
.search .ant-tree {
  color: #fff;
}
.search .ant-tree li .ant-tree-node-content-wrapper {
    color: #d9d9d9;
}
.search .ant-tree.ant-tree-show-line li span.ant-tree-switcher {
    background: transparent;
    color:#faad14;
    
}
.in-content {
  padding: 10px;
}
</style>